.slide:global(-enter) {
  transform: translateX(1000px);
}
.slide:global(-enter-active), .slide:global(-leave-active) {
  transition: all 0.3s;
}
.slide:global(-leave-to) {
  transform: translateX(-1000px);
}
.slide-reverse:global(-enter) {
  transform: translateX(-1000px);
}
.slide-reverse:global(-enter-active), .slide-reverse:global(-leave-active) {
  transition: all 0.3s;
}
.slide-reverse:global(-leave-to) {
  transform: translateX(1000px);
}

:root {
  --sat: env(safe-area-inset-top);
  --sar: env(safe-area-inset-right);
  --sab: env(safe-area-inset-bottom);
  --sal: env(safe-area-inset-left);
}

