.create-addition :global .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}
.create-addition :global .vs-hide {
  visibility: hidden;
}
.create-addition :global .schedule-group-wrap {
  display: flex;
  padding: 24px 22px;
  background-color: white;
  border: 1px solid #dcdfe6;
}
.create-addition :global .schedule-group-wrap .schedule {
  position: relative;
  flex: 1;
}
.create-addition :global .schedule-group-wrap .schedule svg {
  position: absolute;
  left: -40px;
  top: 62px;
  color: #c6c6ba;
  font-size: 27px;
}
@media screen and (max-width: 481px) {
  .create-addition :global .schedule-group-wrap .schedule svg {
    top: 77px;
  }
}
.create-addition :global .schedule-group-wrap .schedule + .schedule {
  margin-left: 50px;
}
.create-addition :global .schedule-group-wrap .schedule > div:first-of-type {
  margin-bottom: 26px;
  letter-spacing: 1.4px;
}
.create-addition :global .schedule-group-wrap .schedule .date {
  margin-bottom: 25px;
}
.create-addition :global .schedule-group-wrap .schedule .date .today {
  display: inline-block;
  font-size: 12px;
  letter-spacing: 1.2px;
  margin-bottom: 10px;
}
.create-addition :global .schedule-group-wrap .schedule .date .time {
  font-size: 20px;
  color: #EA6A2A;
  letter-spacing: 2px;
  line-height: 18px;
  font-weight: 500;
}
.create-addition :global .schedule-group-wrap .schedule .service .title {
  display: inline-block;
  margin-bottom: 5px;
  letter-spacing: 1.4px;
}
.create-addition :global .schedule-group-wrap .schedule .service .size {
  font-size: 12px;
  color: #726f65;
}
.create-addition :global .schedule-group-wrap .schedule .no-booking {
  color: #d0d0d0;
  letter-spacing: 1.4px;
}
.create-addition :global .alert {
  margin-top: 15px;
  margin-bottom: 47px;
  padding: 15px 20px;
  letter-spacing: 1.4px;
  background-color: #FDD7AA65;
}
.create-addition :global .alert.danger {
  color: #DC2A2A;
  background-color: #e35d5d17;
}
@media screen and (max-width: 481px) {
  .create-addition :global .el-input__inner {
    background-color: white;
    border-color: #dcdfe6;
    border-radius: 8px;
  }
  .create-addition :global .footer {
    display: flex;
    flex-direction: column-reverse;
  }
  .create-addition :global .footer .el-col + .el-col {
    margin-bottom: 10px;
  }
}

.addItems {
  border: 1px solid #dcdfe6;
}
.addItems :global .wrap {
  max-height: 350px;
  overflow: auto;
  background-color: white;
}
.addItems :global .wrap::-webkit-scrollbar {
  display: block;
  width: 4px;
}
.addItems :global .wrap::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #d8d8d8;
}
.addItems :global .wrap::-webkit-scrollbar-track {
  background: #f5f5f5ed;
}
.addItems :global .wrap .item:last-child .el-checkbox {
  border-bottom: none;
}
.addItems :global .wrap .item .el-checkbox {
  width: 100%;
  display: flex;
  align-items: center;
  border: none;
  border-bottom: 1px solid #dcdfe6;
  padding: 18px 22px;
  height: auto;
}
.addItems :global .wrap .item .el-checkbox .el-checkbox__label {
  display: flex;
  justify-content: space-between;
  flex: 2;
  letter-spacing: 1.4px;
  align-items: center;
}
.addItems :global .wrap .item .el-checkbox .el-checkbox__label .name {
  display: inline-block;
  min-width: 90px;
}
.addItems :global .wrap .item .el-checkbox .el-checkbox__label br {
  display: none;
}
@media screen and (max-width: 481px) {
  .addItems :global .wrap .item .el-checkbox .el-checkbox__label br {
    display: inherit;
  }
  .addItems :global .wrap .item .el-checkbox .el-checkbox__label .size {
    color: #d0d0d0;
  }
}
.addItems :global .wrap .item .el-checkbox .el-checkbox__label .size {
  font-size: 12px;
}
.addItems :global .wrap .item .el-checkbox .el-checkbox__inner {
  width: 18px;
  height: 18px;
}
.addItems :global .wrap .item .el-checkbox .el-checkbox__inner::after {
  height: 9px;
  width: 5px;
  border-width: 2px;
}
.addItems :global .total {
  display: flex;
  justify-content: space-between;
  padding: 20px 25px;
  background-color: #F1F9FB;
  line-height: 1;
  letter-spacing: 1.4px;
  border-top: 1px solid #dcdfe6;
}

.payment-methods :global .el-radio {
  margin: 5px !important;
}
.payment-methods :global .vacc-disabled {
  margin-bottom: 20px;
}
.payment-methods :global .vacc-disabled::after {
  content: "(服務前一日不提供)";
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  color: #c2c6ce;
}